/* 设置滚动条的样式 */
::-webkit-scrollbar {
    width:0px;
}
/* 滚动槽 */
::-webkit-scrollbar-track {
    border-radius:0px;
}
/* 滚动条滑块 */
::-webkit-scrollbar-thumb {
    border-radius:0px;
    background:#0C1121;
    -webkit-box-shadow:#8C85E6;
}
::-webkit-scrollbar-thumb:window-inactive {
    background: rgba(175, 190, 255, 0.4);
}
