/*头部小于992px开始相应*/
@media screen and (max-width:992px){
    .nav-header{
        position:relative;
    }
    .nav-header .col-md-4
    {
        display:none;

    }
    .nav-header .col-md-6
    {
        display:none;

    }
    .res-992-m-menu{
        display:block;

    }
     .res-992-m-menu span{
        float:right;
        line-height:64px;
        color:#fff;
        font-size:24px;
     }
    .res-992-m-menubox{
        position: absolute;
        top:100%;
        left:0;
        height:auto;
        right:0;
        overflow:auto;
        background:#fff;
        box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        z-index:99999;
    }
     .res-992-m-menubox ul{
        padding:0 15px;
     }
     .res-992-m-menubox ul li{
        padding-left:15px;
        height:32px !important;
        display:flex;
        justify-content:flex-start;

     }
     .res-992-m-menubox ul li a{
        height:32px !important;
        line-height:32px;
        color: rgba(0, 0, 0, 0.65);
     }
      .res-992-m-menubox ul li:hover{
        background-color: rgba(0, 0, 0, 0.04);
      }
     .res-992-m-menubox ul li a:hover{
        color: rgba(0, 0, 0, 0.65);
     }
     .res-992-m-menubox li span{
        line-height:32px;
        color: rgba(0, 0, 0, 0.65);
     }
     .res-992-m-menubox  .dropdown .dropdown-menu{
        width:100%;
        border: 0;
     }

     /*table 自适应*/
     .table-responsive{
        margin:0;
        padding:0;
     }


}

